import { ParaphraseProvider } from "./model/paraphrase/hooks/ParaphraseContext";
import { useParaphrase } from "./model/paraphrase/hooks/useParaphrase";
import { Events } from "./utils/events";
import EventEmitter from "eventemitter3"
import { diffWords } from 'diff';
import { useEffect } from "react";
import { SelectedMatchStyles } from "./components/SelectedMacthStyles";

const Paraphrase = () => {

  return (
    <ParaphraseProvider>
      <div className="paraphrase-area" style={{ maxWidth: '768px', margin:'auto'}}>
          <div><InternalContent /></div>
      </div>
      <SelectedMatchStyles />
    </ParaphraseProvider>
  );
};

function InternalContent() {

  const { state, setParaphraseText } = useParaphrase() 
    
  useEffect(() => {
    const eventEmitter = window._correctorEvents || (window._correctorEvents = new EventEmitter())

    eventEmitter.on<Events>('set-text', (e) => {
      setParaphraseText(e.segment)
  })    
  }, [setParaphraseText])
  
  if (!state) {
    return <></>
  }

  return <>
    <h2>{state.text}</h2>
    <Alternatives alternatives={state.alternatives} />
  </>
}

function Alternatives({ alternatives }: { alternatives: { text: string }[]|undefined}) {
  if (alternatives) {
    return (<>
      <h3>Alternatives:</h3>
      <ul>
      { alternatives.map((a,i) => {
        const k = `alt-${i}`
        return <Alternativa parentKey={k} key={k} {...a} />
      })}
    </ul>
    </>)
  }
}

function compareText(key: string, text1: string, text2: string): JSX.Element[] {
    const differences = diffWords(text1, text2);
    return differences.map((part, index) => {
        const k = `${key}-${index}`
        if (part.added) {
            const fontWeight = 'bold';
            return <span key={k} style={{ fontWeight }}>{part.value}</span>;
        }

        if (part.removed) {
            return <></>;
        }

        const color = 'black';
        return <span key={k} style={{ color }}>{part.value}</span>;
    });
}


function Alternativa({ text, parentKey }: { text: string, parentKey: string }) {
    const { selectAlternative, state } = useParaphrase();

    if (!state) {
      return <></>
    }

    const differences = compareText(parentKey, state.text, text);

    return (
        <li>
            <a style={{ color: "black" }} onClick={(e) => {
                selectAlternative(text);
                e.preventDefault();
            }}>
                {differences}
            </a>
        </li>
    );
}


export default Paraphrase;
 
