import { useEffect, useState } from "react"
import EventEmitter from "eventemitter3"
import { Events } from "../utils/events"


export function SelectedMatchStyles() {

    const [ matchId, setMatchId ] = useState<string|null>(null) 
      
    useEffect(() => {
      const eventEmitter = window._correctorEvents || (window._correctorEvents = new EventEmitter())
  
      eventEmitter.on<Events>('set-selected-match', (e) => {
        setMatchId(e)
      })    
    }, [])
    
    if (!matchId) {
      return <></>
    }
  
    return <>
        <style>{`
            [data-match-id="${matchId}"] {
                border-bottom: purple solid 3px;
                  }
        `}</style>
    </>
  }
  